'use client';
import { useState } from 'react';

const faqs = [
  {
    question: 'What services do you offer?',
    answer:
      'We offer web design, development, SEO, mobile app solutions, and more tailored to your business.',
  },
  {
    question: 'How long does a project take?',
    answer:
      'Project duration varies by scope. Most are completed within 2–6 weeks.',
  },
  {
    question: 'Do you provide post-launch support?',
    answer:
      'Yes! We offer maintenance, updates, and ongoing support after launch.',
  },
  {
    question: 'How can I get a quote?',
    answer:
      'Just contact us with your requirements, and we’ll send you a tailored quote within 24 hours.',
  },
];

export default function Services_faqs() {
  const [openIndex, setOpenIndex] = useState(null);

  const toggle = (idx) => {
    setOpenIndex(openIndex === idx ? null : idx);
  };

  return (
    <div className="bg-white py-12 px-4 md:px-10 lg:px-20">
      <div className="max-w-4xl mx-auto">
        <h2 className="text-3xl md:text-4xl font-bold mb-8 text-center">Frequently Asked Questions</h2>
        <div className="space-y-4">
          {faqs.map((faq, idx) => (
            <div
              key={idx}
              className="border border-gray-200 rounded-lg shadow-sm"
            >
              <button
                onClick={() => toggle(idx)}
                className="w-full text-left px-6 py-4 focus:outline-none flex justify-between items-center"
              >
                <span className="text-[18px] font-semibold text-gray-800">
                  {faq.question}
                </span>
                <span className="text-xl">{openIndex === idx ? '-' : '+'}</span>
              </button>
              <div
                className={`px-6 pb-4 text-gray-600 text-sm leading-relaxed transition-all duration-300 ease-in-out ${
                  openIndex === idx ? 'block' : 'hidden'
                }`}
              >
                {faq.answer}
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
